<template>
  <div>
    <div class="container">
      <div class="grid">
        <div v-for="(row, rowIndex) in field" :key="rowIndex" class="row">
          <div v-for="(cell, cellIndex) in row" :key="cellIndex" class="cell" :class="{ alive: cell === 1 }"></div>
        </div>
      </div>
    </div>
    <div v-if="gameStarted && gameOver" class="game-over">Game over</div>
    <button @click="startGame">Start</button>
    <button v-if="isRunning" @click="pauseGame">Pause</button>
    <button @click="restartGame">Restart</button>
  </div>
</template>

<script>
import axios from 'axios';

const api = axios.create({
  baseURL: 'http://0.0.0.0:5000',
});

export default {
  data() {
    return {
      field: [],
      isRunning: false, // Флаг для отслеживания состояния игры (запущена или остановлена)
      intervalId: null, // Идентификатор интервала для остановки игры
      gameStarted: false, // Флаг для отслеживания начала игры
      gameOver: false, // Флаг для отслеживания окончания игры
    };
  },
  methods: {
    getFieldState() {
      api.get('/game_of_life')
        .then(response => {
          this.field = response.data;
          if (this.field.length === 0) {
            this.gameOver = true; // Устанавливаем флаг окончания игры
            clearInterval(this.intervalId); // Останавливаем игру, если пришел пустой список
          }
        })
        .catch(error => {
          console.error(error);
        });
    },
    restartGame() {
      this.gameStarted = false;
      this.gameOver = false;
      this.field = [];
      api.post('/new_game')
        .then(response => {
          console.log(response.data.message);
        })
        .catch(error => {
          console.error(error);
        });
    },
    startGame() {
      this.gameStarted = true; // Устанавливаем флаг начала игры
      this.gameOver = false; // Сбрасываем флаг окончания игры
      this.getFieldState(); // Выполняем запрос состояния при старте игры
      this.isRunning = true; // Устанавливаем флаг запущенной игры
      this.intervalId = setInterval(() => {
        this.getFieldState();
      }, 100);
    },
    pauseGame() {
      clearInterval(this.intervalId); // Останавливаем интервал
      this.isRunning = false; // Устанавливаем флаг остановленной игры
    },
  },
};
</script>

<style>
.container {
  display: flex;
  justify-content: center;
}
.grid {
  display: flex;
  flex-direction: column;
}

.row {
  display: flex;
}

.cell {
  width: 15px;
  height: 15px;
  border: 1px solid #ccc;
}

.cell.alive {
  background-color: #000;
}

.game-over {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  height: 100px;
}
</style>
